.button {
  color: #fff;
  height: 24px;
  background-color: #d41443;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  transition: color .3s cubic-bezier(.57, .21, .69, 1.25), background-color .3s cubic-bezier(.57, .21, .69, 1.25);
  display: inline-flex;
  position: relative;
}

.button:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  border: 1px solid #d9d9d9;
  border-radius: 20px;
  transition: opacity .3s cubic-bezier(.57, .21, .69, 1.25), transform .3s cubic-bezier(.57, .21, .69, 1.25);
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(1.2);
}

.button:hover:after, .button:focus:after {
  opacity: 1;
  transform: scale(1);
}

.button--white {
  color: #d41443;
  background-color: #fff;
  transition: color .3s cubic-bezier(.57, .21, .69, 1.25), background-color .3s cubic-bezier(.57, .21, .69, 1.25);
  position: relative;
}

.button--white:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  border: 1px solid #c7133e;
  border-radius: 22px;
  transition: opacity .3s cubic-bezier(.57, .21, .69, 1.25), transform .3s cubic-bezier(.57, .21, .69, 1.25);
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(1.2);
}

.button--white:hover:after, .button--white:focus:after {
  opacity: 1;
  transform: scale(1);
}

.button:hover, .button:focus {
  color: #d41443;
  background-color: #ecebeb;
}

.button--white:hover, .button--white:focus {
  color: #fff;
  background-color: #c7133e;
}

